	module.exports = {"data":{"markdownRemark":{"html":"<p>いつもチャットで私が始めにするアイサツです。 と、そんな理由でコンニチワ。またはA HAPPY NEW YEAR IN 2000(遅すぎ)。YASMことLuv-Reverseです。……どっちも「野菜村」をもぢっただけなんだケド。最近はベーマガ投稿も全くやっていなく、今年に至っては<a href=\"/2954\">年賀状</a>すらロクに出していませんでした。実際出したのは闘技場などでお世話になった方十数名にガバッとメールで送っただけです。しかも数秒～数時間で届くハズのメールにも関わらずX'mas前に出してしまった。(メリークリスマス!! あけましておめでとう……って感じで←多忙人を超えて<em>バカ</em>)実際一部の人に「気ィ早すぎ」って言われたし……。  知ってると思うケド、私、今受験なんやね……。どおりゃぁぁぁッ!! と机に向かったは良いが、模試&#x26;センターで偏差値28～31、志望の大学のボーダーは47。どおりゃぁぁぁッ!! と枕に向かう私でした。こんなかんじで無駄な努力を尽くしている毎日です。</p>\n<h2>最近の動向</h2>\n<ul>\n<li>尚美学園大もオチました……TT;)</li>\n<li>こっそりと<a href=\"/2958\">牛チョップ</a>なるゲームを作ってみました。</li>\n<li>知ってるかもしれませんが、ウチにはネット環境がありません。いつもネットはまんが喫茶でやっているのです。</li>\n<li>プリンタ(EPSON MJ-800C)がぶっ壊れました。稼働中に停電したせいか、どうやらファームがすっとんだようす。</li>\n</ul>","frontmatter":{"cover":null,"date":"2000-02-17T02:00:00+09:00","strDate":"2000/2/16","title":"ちゃお♪(OoO)ﾉ"}}},"pathContext":{"path":"/ja/blog/2000/02/17-chao/","langKey":"ja"}}

